<template>
	<div id="left_panel2">
		<div class="left_panel">
			            	<div class="content">
				            	<div id="jquery-accordion-menu" class="jquery-accordion-menu red">
									<div class="jquery-accordion-menu-header" id="form"></div>
									<ul id="demo-list">
										<li v-for="i in father" :class="i.classify" >
											<a href="#">
												<i class="fa fa-home"></i>
												{{i.text}}
											</a>
											<ul class="submenu" v-for="c in child"  v-if="i.id==c.belongto">
													<li>
														<a href="#">{{c.text}} </a><span class="jquery-accordion-menu-label">{{c.num}}</span>
														<ul class="submenu">
															<li  v-for="d in childs"  v-if="c.id==d.belongto"><a href="#">{{d.text}}</a></li>
														</ul>
													</li>
											</ul>
										</li>
									</ul>
				            	</div>
			            	</div>
			        </div>
	</div>
</template>

<script>
import '../assets/js/jquery-accordion-menu.js'
import '../assets/css/jquery-accordion-menu.css'
export default{
	data(){
		return {
					father:[
								{text:"HOME",},
								{text:"Text"},
								{text:"FFF",id:"11"},
								{text:"TTTT",id:"22"},
							],
					child:[
						{text:"",id:"111",belongto:"11",num:23},
						{text:"2",id:"113",belongto:"22",num:55}
					],
					childs:[
					{text:"--",id:"111--",belongto:"111"},
					{text:"--",id:"111--",belongto:"111"},
					{text:"--",id:"111--",belongto:"111"},
					{text:"--",id:"111--",belongto:"111"},
					{text:"--",id:"111--",belongto:"111"},
					{text:"--",id:"11--",belongto:"113"}
					],
		}
	},
	beforeCreated(){
		if(!data.father[0]["classify"]){
					data.father[0]["classify"]="active";
				}
	},
	mounted(){
		$("#demo-list li").click(function(){
	    	
	    	$(this).find(".active").removeClass("active");
	    	$(this).siblings().removeClass("active");
			$(this).addClass("active");
		})
		jQuery("#jquery-accordion-menu").jqueryAccordionMenu();
		
		$.expr[":"].Contains = function(a, i, m) {
			return (a.textContent || a.innerText || "").toUpperCase().indexOf(m[3].toUpperCase()) >= 0;
		};
		
		function filterList(header, list) {
			//@header 
			//@list 
			//
			var form = $("<form>").attr({
				"class":"filterform",
				action:"#"
			}), input = $("<input>").attr({
				"class":"filterinput",
				type:"text"
			});
			$(form).append(input).appendTo(header);
			$(input).change(function() {
				var filter = $(this).val();
				if (filter) {
					$matches = $(list).find("a:Contains(" + filter + ")").parent();
					$("li", list).not($matches).slideUp();
					$matches.slideDown();
				} else {
					$(list).find("li").slideDown();
				}
				return false;
			}).keyup(function() {
				$(this).change();
			});
		}
		
		filterList($("#form"), $("#demo-list"));
	}
}
</script>

<style>
	*{box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box;}
.content{width:100%;}
.filterinput{
	background-color:rgba(249, 244, 244, 0);
	
    /*background-image:url(../../images/Main/search_boxp.png);
 	border-radius:5px;
	background-repeat: no-repeat;
	background-size:cover;*/
	width:90%;
	border-radius:8px;
	font-size:1em;
	line-height: 20px;
	height:24px;
	border:1px solid #64798f;
	text-indent:0.5em;
	font-weight:normal;
	color:#FFF;
}
#demo-list a{
	overflow:hidden;
	text-overflow:ellipsis;
	-o-text-overflow:ellipsis;
	white-space:nowrap;
	width:100%;
}
</style>


