<template>
	<ul class="MagOrg" id="MagOrg">
		<li v-for="i in first"  v-on:click="fClick"><span class="symbol"></span><span class="user"></span>{{i.text}}
			<ul class="childOrg" >
				<li v-for="c in second" v-if="c.belongto==i.id"><span class="icon"></span>{{c.text}}</li>          	
			</ul>
		</li>
	</ul>
</template>

<script>
	import 'jquery'
	export default{
			data(){
				return {
					first:[
					{text:"",id:"1"},
					{text:"2",id:"2"},
					{text:"3",id:"3"},
					{text:"4",id:"4"},
					],
					second:[
						{text:"11",id:"22",belongto:"1"},
						{text:"11",id:"22",belongto:"1"},
						{text:"11",id:"22",belongto:"1"},
						{text:"11",id:"22",belongto:"1"},
						{text:"11",id:"22",belongto:"1"},
					]
				}
			},
			methods:{
				fClick:function(){
					$(this).addClass("active");
					if($(this).hasClass("active")){
						$(this).removeClass("active");
						if($(this).find("ul")){
							$(this).find("ul").slideUp();
							$(this).find(".active").removeClass("active");
						}
						
					}else{
						$(this).siblings().removeClass("active");
						$(this).addClass("active");
						if($(this).find("ul")){
							$(this).find("ul").slideDown();
						
						}
					}
					 
					 
				}
			},
	}
</script>

<style>
	/**/
.main div.right_zero{
	height: 100%;
}
.main div.right_zero{
	height:100%;
}
.main .main_con div.table{
    margin-left: 10px;
    margin-bottom: 0px;
    height: calc(100% - 214px);
}
ul.MagOrg{
	list-style: none;
	margin:0px;
	padding:0px;
	color:#b9cdd7;
	width:95%;
	cursor:pointer;
	border-radius: 5px;
    overflow: hidden;
}
ul.MagOrg>li{
	background-color: #2a404e;
	margin-bottom:1px;
	line-height:2em;
}
ul.childOrg{
	list-style: none;
	margin:0px;
	padding:0px;
	padding-left:32px;
	color:#b9cdd7;
	cursor:pointer;
	background-color:#223642;
}
ul.childOrg li{
	background-color:#223642;
}
ul.childOrg li.active{
	background-color:#587081;
}
ul.MagOrg li span{
	display: block;
	float:left;
	height:16px;
	width:16px;
	margin-top:8px;
	background-size:contain;
	background-repeat: no-repeat;
	background-position: center;
}
ul.MagOrg li span.symbol{
	background-image: url(../../assets/Manage/add.png);
	margin-left:1em;
}
ul.MagOrg li span.user{
	background-image: url(../../assets/Manage/user.png);
}
ul.MagOrg li span.icon{
	background-image: url(../../assets/Manage/to_right.png);
	height: 10px;
	width:10px;
	margin-top:12px;
}
.main .main_con div.table {
    padding: 10px;
    background-color: #1f2e3a;
}
</style>


